h1.b-w3ltxt {
    font-size: 3.5em;
    color: #fff;
    font-weight: 600;
    text-shadow: 1px 1px 0px #333;
}
h2.b-w3ltxt {
    font-size: 3em;
    letter-spacing: 3px;
    font-weight: 300;
    color: #fff;
}
.content__container {
  overflow: hidden;
  height: 60px;
  padding: 0 0px;
  position: relative;
  top: -62px;  
}
.content__container__text {
  display: inline;
  float: left;
  margin: 0;
}
.content__container__list {
  margin-top: 2px;
  padding-left: 250px;
  text-align: left;
  list-style: none;
  -webkit-animation-name: change;
  -webkit-animation-duration: 10s;
  -webkit-animation-iteration-count: infinite;
  animation-name: change;
  animation-duration: 10s;
  animation-iteration-count: infinite;
}
.content__container__list__item {
  line-height: 60px;
  margin: 0;
}
.testi-w3pvt-grid {
    min-height: 230px;
}
@-webkit-keyframes opacity {
  0%, 100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}
@-webkit-keyframes change {
  0%, 12.66%, 100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  16.66%, 29.32% {
    -webkit-transform: translate3d(0, -25%, 0);
            transform: translate3d(0, -25%, 0);
  }
  33.32%,45.98% {
    -webkit-transform: translate3d(0, -50%, 0);
            transform: translate3d(0, -50%, 0);
  }
  49.98%,62.64% {
    -webkit-transform: translate3d(0, -75%, 0);
            transform: translate3d(0, -75%, 0);
  }
  66.64%,79.3% {
    -webkit-transform: translate3d(0, -50%, 0);
            transform: translate3d(0, -50%, 0);
  }
  83.3%,95.96% {
    -webkit-transform: translate3d(0, -25%, 0);
            transform: translate3d(0, -25%, 0);
  }
}
@keyframes opacity {
  0%, 100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}
@keyframes change {
  0%, 12.66%, 100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  16.66%, 29.32% {
    -webkit-transform: translate3d(0, -25%, 0);
            transform: translate3d(0, -25%, 0);
  }
  33.32%,45.98% {
    -webkit-transform: translate3d(0, -50%, 0);
            transform: translate3d(0, -50%, 0);
  }
  49.98%,62.64% {
    -webkit-transform: translate3d(0, -75%, 0);
            transform: translate3d(0, -75%, 0);
  }
  66.64%,79.3% {
    -webkit-transform: translate3d(0, -50%, 0);
            transform: translate3d(0, -50%, 0);
  }
  83.3%,95.96% {
    -webkit-transform: translate3d(0, -25%, 0);
            transform: translate3d(0, -25%, 0);
  }
}
.dynamic_span_slider {
    display: block;
}
.static_span_slider {
    display: none;
}
ul.menu > li > a {
    color: #aaa;
}
ul.menu > li.active > a {
    color: #fff;
    font-weight: bold;
}
@media(max-width:1080px) {
    .dynamic_span_slider {
        display: none;
    }
    .static_span_slider {
        display: inline;
    }
}
@media all and (max-width : 991px) {
  ul.menu > li.active > a {
      color: #999;
      font-weight: bold;
  }
}
@media all and (max-width : 768px) {
  .content__container__list {
    margin-top: 6px;
    padding-left: 200px;
  }
  .content__container {
    overflow: hidden;
    height: 60px;
    padding: 0 0px;
    position: relative;
    top: -56px;  
  }
}
@media all and (max-width : 480px) {
  h1.b-w3ltxt {
      font-size: 2.5em;
      color: #fff;
      font-weight: 600;
      text-shadow: 1px 1px 0px #333;
  }
  h2.b-w3ltxt {
      font-size: 2em;
      letter-spacing: 3px;
      font-weight: 300;
      color: #fff;
  }
}
